import { Directive, HostListener } from '@angular/core';
import { PopoverController,Events } from 'ionic-angular';
import {PopoverPage} from '../notifications/popover';
import { NotificationProvider } from '../../providers/notification/notification';

@Directive({
    selector: '[notifications]'
})
export class NotificationsDirective {
    

    constructor(public popoverCtrl: PopoverController, public events: Events, private notificationProvider:NotificationProvider) {    
    
        this.events.subscribe("notification:received", (notification) => {

            notification.read = false;         
            this.notificationProvider.addNotification(notification).then(() => {
                this.events.publish("notification:updatecount");
            }) 
        });     
    
    }

    @HostListener('click', ['$event']) onClick(ev) {
        this.presentPopover(ev)
    }  
    
    
    public presentPopover(ev) {
        let popover = this.popoverCtrl.create(PopoverPage, {}, {cssClass: "notifications-popover"});
        popover.present({
            ev: ev
        });

    }  

}




