import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams, AlertController, ToastController,LoadingController, Events } from 'ionic-angular';
import { ContractorJobsPage } from '../contractor-jobs/contractor-jobs';
import { CustomerJobsPage } from '../customer-jobs/customer-jobs';
import { AuthenticationProvider } from '../../providers/authentication/authentication';
import { ProfileProvider } from '../../providers/profile/profile';

import { StatusBar } from '@ionic-native/status-bar';

import { Camera, CameraOptions } from '@ionic-native/camera';

import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

import {AppSettings} from '../../app/app.settings';

import { GoogleMapsComponent } from '../../components/google-maps/google-maps';

/**
 * Generated class for the SignupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class SignupPage {

    public profile:any;
    public user:any;
    public stage:number;
    
    @ViewChild(GoogleMapsComponent) mapComponent: GoogleMapsComponent;

    constructor(public navCtrl: NavController, public navParams: NavParams, public statusBar: StatusBar, private events: Events, public authProvider: AuthenticationProvider, public profileProvider: ProfileProvider, private alertCtrl: AlertController, private camera: Camera,private transfer: FileTransfer, private file: File, private toastCtrl: ToastController, public loadingCtrl: LoadingController) {
        
        
        this.profile = {avatar:"https://www.drupal.org/files/issues/default-avatar.png", radius:20, latitude:-36.89479598377308, longitude: 174.75231011873302};
        this.user = {email:"", password:"", permission:"customer", loading:false};
        this.stage = 1;
        
        this.events.subscribe("location:updated", (event) => {
            this.profile.latitude = event.latLng.lat();
            this.profile.longitude = event.latLng.lng();
        });
        
    }
    
    ionViewDidEnter() {
        this.statusBar.backgroundColorByHexString("#3c5a01");
    }    
    
    public nextStage(){
        
        
        if (this.stage === 2 && (!this.profile.public_email || !this.validateEmail(this.profile.public_email))){
            let alert = this.alertCtrl.create({
                title: 'Invalid Email',
                subTitle: 'A valid email is required to sign up.',
                buttons: ['Dismiss']
            });
            alert.present();            
            return;
        }
        else if (this.stage === 2 && this.user.permission === "customer"){
            this.stage +=2;
        }
        else{
            this.stage +=1;
        }

    }
    
    public changeStage(stage){
        this.stage = stage;
       
    }
    
    public selectUserType(userType){
        this.user.permission = userType;
    }
    
    public signup(){
        this.user.loading = true;
        this.user.error = "";
        this.user.email = this.profile.public_email;
        this.authProvider.register(this.user).then(() => {
            this.profileProvider.updateProfile(this.profile).then(() => {
                this.user.loading = false;
                this.authProvider.getUserData();
                this.navigateToJobs();
                
            }).catch(() => {
                this.user.loading = true;
            })
        }).catch((e) => {
            this.user.loading = false;
            if (e && e.error){
                for (let error of e.error.error.errors){
                    this.user.error = this.user.error + error + " ";
                }                
            }
            else{
                this.user.error = e.message;
            }

            
        })
  
    }
    
    private navigateToJobs(){
        if (this.user.permission === 'customer'){
            this.navCtrl.setRoot(CustomerJobsPage);
        }
        else if (this.user.permission === 'contractor'){
            this.navCtrl.setRoot(ContractorJobsPage);
        }        
    }
    
    private validateEmail(email) {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }
    

    public updateRadius(){
        this.mapComponent.updateRadius(this.profile.radius, this.profile.latitude, this.profile.longitude);
    }
    
    
    public changeAvatar(){
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.FILE_URI,  
            encodingType: this.camera.EncodingType.JPEG,    
            quality: 25
        }

        this.camera.getPicture(cameraOptions)
            .then((image) => {
                
                let loading = this.loadingCtrl.create({
                    content: 'Your image is being uploaded. Please wait...'
                });      
                
                loading.present();            

                const fileTransfer: FileTransferObject = this.transfer.create();                
                
                let options: FileUploadOptions = {
                    fileKey:"fileToUpload",
                    fileName: Math.random().toString(36).substring(2),
                    params: {},
                    mimeType : "image/jpeg"
                }

                fileTransfer.upload(image, encodeURI(AppSettings.apiUrl + "/upload"), options).then((data) => {
                    loading.dismiss();
                    this.profile.avatar = data.response;
                   
                }, (err) => {
                    loading.dismiss();
                    let alert = this.alertCtrl.create({
                        title: "Error",
                        subTitle: "There was a problem uploading your image",
                        message: JSON.stringify(err),
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel'
                            }
                        ]
                    });
                    alert.present();                    
                })
      
                
            }, 
            err => {
                //console.log(err)
            });   
    }     



}



