import { Component, Input, Renderer2, ElementRef, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';
import { Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';

var google:any;
 
@Component({
  selector: 'google-maps',
  templateUrl: 'google-maps.html'
})
export class GoogleMapsComponent {
 
    @Input('apiKey') apiKey: string;
    @Input('latitude') latitude: string;
    @Input('longitude') longitude: string;
    @Input('radius') radius: string;
 
    public map: any;
    public markers: any[] = [];
    public circles: any[] = [];
    private mapsLoaded: boolean = false;
    private profile:any;
    
 
    constructor(private renderer: Renderer2, private element: ElementRef, @Inject(DOCUMENT) private _document, public storage: Storage, private events:Events){
        
    }
 
    ngOnInit(){
        
        this.storage.get("user").then((user) => {
            if (user){
                this.profile = user.profile;
            }
            else{
                this.profile = {latitude:-36.89479598377308,longitude:174.752310118,radius:20}
            }
            
            if (this.latitude){
                this.profile.latitude = this.latitude;
            }
            
            if (this.longitude){
                this.profile.longitude = this.longitude;
            }
            
            if (this.radius){
                this.profile.radius = this.radius;
            }
            
            this.init().then((res) => {
                console.log("Google Maps ready.")
            }, (err) => {   
                console.log(err);
            });
        });
 
    }
 
    private init(): Promise<any> {
 
        return new Promise((resolve, reject) => {
 
            this.loadSDK().then((res) => {
 
                this.initMap().then((res) => {
                    resolve(true);
                }, (err) => {
                    reject(err);
                });
 
            }, (err) => {
 
                reject(err);
 
            });
 
        });
 
    }
 
    private loadSDK(): Promise<any> {
 
        console.log("Loading Google Maps SDK");
 
        return new Promise((resolve, reject) => {
 
            if(!this.mapsLoaded){
                
                this.injectSDK().then((res) => {
                        resolve(true);
                    }, (err) => {
                        reject(err);
                    });
                    
            } else {
                reject('SDK already loaded');
            }
        });

 
    }
 
    private injectSDK(): Promise<any> {
 
        return new Promise((resolve, reject) => {
 
            window['mapInit'] = () => {
                this.mapsLoaded = true;
                google = window["google"];
                resolve(true);
                
            }
            
            if (document.getElementById("googleMaps")){
                this.mapsLoaded = true;
                google = window["google"];
                resolve(true);
            }
            
            else{
                
                let script = this.renderer.createElement('script');
                script.id = 'googleMaps';

                if(this.apiKey){
                    script.src = 'https://maps.googleapis.com/maps/api/js?key=' + this.apiKey + '&callback=mapInit';
                } else {
                    script.src = 'https://maps.googleapis.com/maps/api/js?callback=mapInit';      
                }

                this.renderer.appendChild(this._document.body, script);
            }

        });
 
    }
 
    private initMap(): Promise<any> {
 
        return new Promise((resolve, reject) => {

                let latLng = new google.maps.LatLng(this.profile.latitude, this.profile.longitude);
 
                let mapOptions = {
                    center: latLng,
                    zoom: 8
                };
 
                this.map = new google.maps.Map(this.element.nativeElement, mapOptions);
                
                this.map.addListener('click',(event) => {
                    this.addMarker(event.latLng);
                    this.events.publish("location:updated", event);
                });           
                

                this.addMarker(latLng);   
                
                resolve(true);
 
            });

    }
    
    public updateRadius(radius, lat, lng){
        this.profile.radius = radius;
        this.profile.latitude = lat;
        this.profile.longitude = lng;
        console.log(this.profile);
        let latLng = new google.maps.LatLng(this.profile.latitude, this.profile.longitude);
        this.addMarker(latLng);
    }
 
    public addMarker(latLng): void {

        //clear existing markers
        for (var i = 0; i < this.markers.length; i++) {
            this.markers[i].setMap(null);
        }                
        this.markers = [];
        
        //add the market
        let marker = new google.maps.Marker({
            map: this.map,
            animation: google.maps.Animation.DROP,
            position: latLng
        });
 
        this.markers.push(marker);
 
        
        //clear existing circles
        for (var x = 0; x < this.circles.length; x++) {
            this.circles[x].setMap(null);
        }                
        this.circles = [];        
               
        //add the circle
        
        let circle = new google.maps.Circle({
          strokeColor: '#79b402',
          strokeOpacity: 0.8,
          strokeWeight: 2,
          fillColor: '#79b402',
          fillOpacity: 0.35,
          map: this.map,
          center: latLng,
          radius: (this.profile.radius / 6378.1) * 6378100
        });  

        this.circles.push(circle);      
        
 
    }
 
}


