import { Component } from '@angular/core';
import { NavController, ModalController, Platform, NavParams, ViewController, ToastController, AlertController, Events, LoadingController } from 'ionic-angular';

import { Storage } from '@ionic/storage';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';

import { FindAddressModal } from '../../modals/find-address/find-address';

import {AppSettings} from '../../app/app.settings';

import { NativeGeocoder, NativeGeocoderReverseResult, NativeGeocoderForwardResult } from '@ionic-native/native-geocoder';

import * as moment from 'moment';

@Component({
    selector: 'edit-profile',
    templateUrl: 'edit-profile.html'
})

export class EditProfileModal {
    public profile:any;
    constructor(public platform: Platform, public params: NavParams, public modalCtrl: ModalController, public loadingCtrl: LoadingController, public viewCtrl: ViewController, private camera: Camera, public storage: Storage, private alertCtrl: AlertController, public events: Events,private transfer: FileTransfer, private toastCtrl: ToastController, private nativeGeocoder: NativeGeocoder) {
        this.profile = {};
        Object.assign(this.profile, this.params.data.profile);
        this.profile.dob = this.profile.dob ? moment(this.profile.dob).toISOString() : null;
    }

    public update(){
        this.viewCtrl.dismiss(this.profile);
    }

    public dismiss() {
        this.viewCtrl.dismiss();
    }
    
    public changeAvatar(){
        let cameraOptions = {
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
            destinationType: this.camera.DestinationType.FILE_URI,  
            encodingType: this.camera.EncodingType.JPEG,    
            quality: 25
        }

        this.camera.getPicture(cameraOptions)
            .then((image) => {
                
                let loading = this.loadingCtrl.create({
                    content: 'Your image is being uploaded. Please wait...'
                });     
                
                loading.present();               

                const fileTransfer: FileTransferObject = this.transfer.create();                
                
                let options: FileUploadOptions = {
                    fileKey:"fileToUpload",
                    fileName: Math.random().toString(36).substring(2),
                    params: {},
                    mimeType : "image/jpeg"
                }
             
                
                fileTransfer.upload(image, encodeURI(AppSettings.apiUrl + "/upload"), options).then((data) => {
                    loading.dismiss();
                        this.profile.avatar = data.response;                  
                }, (err) => {
                    loading.dismiss();
                    let alert = this.alertCtrl.create({
                        title: "Error",
                        subTitle: "There was a problem uploading your image",
                        message: JSON.stringify(err),
                        buttons: [
                            {
                                text: 'Dismiss',
                                role: 'cancel'
                            }
                        ]
                    });
                    alert.present();                    
                })
      
                
            }, 
            err => {
                //console.log(err)
            });   
    }    
    

}






